import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Link from 'gatsby-link'
import "../styles/courses.scss"
import {graphql} from "gatsby"

const CoursesPage = ({data}) => (
  <Layout>
    <SEO title="Courses" />
    <div className="courses-header pb-5 pt-5">
        <div className="container">
            <h1 className="mb-0">Our Courses</h1>
        </div>
    </div>
    <div className="container mb-5 mt-5">
        {data.allMarkdownRemark.edges.map(course => (
            <div key={course.node.id} className="course pt-4 pb-4 pl-4 pr-4 mb-5">
                <div className="price">{course.node.frontmatter.price}</div>
                <h2>{course.node.frontmatter.title}</h2>
                <p>{course.node.frontmatter.description}</p>
                <div className="level mb-3">Level: {course.node.frontmatter.level}</div>
                <Link key={course.node.id} className="btn btn-outline-secondary text-white" to={course.node.frontmatter.path} role="button">Start Your Journey</Link>
            </div>
        ))}
    </div>
  </Layout>
)

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(filter: { frontmatter:  { type: {eq: "course"}}}) {
      edges {
        node {
          id
          frontmatter {
            title
            path
            description
            price
            level
          }
        }
      }
    }
  }
`

export default CoursesPage
